@import "variables";

@font-face {
  font-family: '#{$icomoon-font-family}';
  src: url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?iu3l1b') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '#{$icomoon-font-family}' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-approved-signal {
	&:before {
		content: $icon-approved-signal;
		color: #635cff;
	}
}
.icon-ic-fb {
	&:before {
		content: $icon-ic-fb;
		color: #425566;
	}
}

.icon-ic-ln {
	&:before {
		content: $icon-ic-ln;
		color: #425566;
	}
}

.icon-ic-drop-down {
	&:before {
		content: $icon-ic-drop-down;
	}
}

.icon-ic-next {
	&:before {
		content: $icon-ic-next;
	}
}

.icon-ic-tw {
	&:before {
		content: $icon-ic-tw;
		color: #425566;
	}
}